body {
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(243deg, #bef8ff, #fcd570);
    color: #424242;
    margin: 0;
    background-repeat: no-repeat;
}
a{
    color:#9e0dba;
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}
ul,li{
    margin: 0;
    padding: 0;
    list-style: none;
}